<template>
  <div class="flex flex-col w-full min-h-full items-center bg-gray-300 container py-5">
    <div class="flex w-full">
      <button class="flex-1 rounded py-4 mr-1 border border-blue-600 transition focus:outline-none font-normal" @click="versiHarga = 'normal'; gantiGambar()" :class="versiHarga == 'normal' ? 'bg-blue-600 text-gray-100' : 'text-blue-600'">Harga Normal</button>
      <button class="flex-1 rounded py-4 ml-1 border border-blue-600 transition focus:outline-none font-normal" @click="versiHarga = 'pulsa'; gantiGambar()" :class="versiHarga == 'pulsa' ? 'bg-blue-600 text-gray-100' : 'text-blue-600'">Harga Pulsa</button>
    </div>
    <div class="flex flex-col w-full my-3">
      <img v-for="gambar in gambars" v-bind:key="gambar.id" :src="'img/'+gambar+'.png'" class="w-full rounded object-cover shadow my-2">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      versiHarga: 'normal',
      gambars: [
        'AOV-normal',
        'CODM-normal',
        'FF-normal',
        'PUBGM-normal',
      ]
    }
  },
  methods: {
    gantiGambar() {
      if ( this.versiHarga == 'normal' ) {
        this.gambars = [
          'AOV-normal',
          'CODM-normal',
          'FF-normal',
          'PUBGM-normal',
        ]
      } else if ( this.versiHarga == 'pulsa' ) {
        this.gambars = [
          'AOV-pulsa',
          'CODM-pulsa',
          'FF-pulsa',
          'PUBGM-pulsa',
        ]
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .transition {
    transition: .2s;
  }
</style>