  methods: {
    kirimBeliDiamond: function(admin) {
      
      var formatPesanan = ""
      var enter = "%0a"
      var nomorHP = ""

      formatPesanan += `Assalamu'alaikum${enter}`
      formatPesanan += `Mau *Top Up* nih${enter}${enter}`
      formatPesanan += `*Nickname*: ${this.nickname}${enter}`
      formatPesanan += `*Game*: ${this.namaGame}${enter}`

      if ( this.namaGame === "Arena Of Valor" ) {
        formatPesanan += `*Jumlah Voucher*: ${this.jumlahBeli}${enter}`
      }
      if ( this.namaGame === "Call Of Duty Mobile" ) {
        formatPesanan += `*Jumlah COD Point*: ${this.jumlahBeli}${enter}`
      }
      if ( this.namaGame === "Free Fire" ) {
        formatPesanan += `*Akun ID*: ${this.akunID}${enter}`
        formatPesanan += `*Jumlah Diamond*: ${this.jumlahBeli}${enter}`
      }
      if ( this.namaGame === "Playerunknown's Battlegrounds Mobile" ) {
        formatPesanan += `*Akun ID*: ${this.akunID}${enter}`
        formatPesanan += `*Jumlah Unknown Cash*: ${this.jumlahBeli}${enter}`
      }

      formatPesanan += `*Metode Pembayaran*: ${this.metodePembayaran}`

      if ( this.namaGame === "Arena Of Valor" || this.namaGame === "Call Of Duty Mobile" ) {
        formatPesanan += `${enter}${enter}_top up game *${this.namaGame}* dilakukan via login, jadi berikan akun anda kepada admin yang bersangkutan_`
      }

      if ( admin === 1 ) {
        nomorHP = "6287817826418" 
        window.open(`https://api.whatsapp.com/send?phone=${nomorHP}&text=${formatPesanan}`, '_blank')
      } else if ( admin === 2 ) {
        nomorHP = "6281283379939" 
        window.open(`https://api.whatsapp.com/send?phone=${nomorHP}&text=${formatPesanan}`, '_blank')
      } else if ( admin === 3 ) {
        nomorHP = "6281315531154" 
        window.open(`https://api.whatsapp.com/send?phone=${nomorHP}&text=${formatPesanan}`, '_blank')
      }

