import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import DaftarHarga from '../views/DaftarHarga.vue'
import BeliDiamond from '../views/BeliDiamond.vue'
import BeliAkun from '../views/BeliAkun.vue'
import JualAkun from '../views/JualAkun.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/daftar-harga',
    name: 'DaftarHarga',
    component: DaftarHarga
  },
  {
    path: '/beli-diamond',
    name: 'BeliDiamond',
    component: BeliDiamond
  },
  {
    path: '/beli-akun',
    name: 'BeliAkun',
    component: BeliAkun
  },
  {
    path: '/jual-akun',
    name: 'JualAkun',
    component: JualAkun
  },
]

const router = new VueRouter({
  mode: 'history',
  linkActiveClass: 'bg-blue-600 text-gray-100',
  routes
})

export default router
